var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'NOMBRE TEMA 3'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-up"}},[_vm._v("En este mismo sentido, la moraleja de la fábula del erizo tiene una relación profunda con las relaciones interpersonales en la infancia, ya que refleja los desafíos y aprendizajes que los niños experimentan al construir vínculos con sus cuidadores y pares (Berger, 2007). En esta etapa, los niños están desarrollando habilidades sociales y emocionales fundamentales, y las dinámicas de acercamiento y distanciamiento descritas en la fábula son especialmente relevantes.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-up"}},[_vm._v("Estudiemos algunos de esos factores:")]),_c('div',{staticClass:"row mb-4"},[_vm._m(4),_c('div',{staticClass:"col-12 col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Necesidad de cercanía emocional"}},[_c('p',[_vm._v("En la infancia, los niños dependen de sus cuidadores para sentirse seguros y protegidos. El apego seguro permite al niño explorar su entorno con confianza, sabiendo que cuenta con una base emocional sólida. La moraleja de la fábula resalta la importancia de esa cercanía emocional, dado que los niños necesitan el \"calor\" de las relaciones afectivas, para su desarrollo emocional y social.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Límites y diferencias individuales"}},[_c('p',[_vm._v("Al igual que los erizos, los niños y sus cuidadores (o pares) tienen características individuales que pueden causar \"roces\" y estos pueden surgir; por ejemplo, cuando un niño expresa sus emociones de forma intensa o cuando no sabe cómo manejar los conflictos que se le presentan en la convivencia con los otros. Enseñarles a reconocer y respetar los límites de los demás desde temprana edad, es clave para fomentar relaciones interpersonales saludables, así como lo plantea Vygotsky (1979): “El aprendizaje y el desarrollo surgen a partir de la interacción social, donde el niño aprende a manejar sus emociones y a entender las perspectivas de los demás\". ")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Aprendizaje gradual de la convivencia"}},[_c('p',[_vm._v("En la infancia, los niños están en un proceso constante de aprendizaje sobre cómo relacionarse con los demás. Esto incluye aprender a compartir, resolver conflictos, negociar y cooperar. Tal como los erizos en la fábula, los niños necesitan tiempo y guía para encontrar ese \"punto medio\" en el que puedan relacionarse sin lastimar a los demás ni sentirse heridos. Goleman (1995), lo respalda cuando habla de que es necesario para los niños y las personas aprender a reconocer las emociones propias y saber cómo responder a las emociones del otro, solo así se pueden construir relaciones positivas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Respeto mutuo y empatía"}},[_c('p',[_vm._v("En esta fábula, se destaca la necesidad de respetar las diferencias de los demás mientras se mantiene una conexión afectiva. En la infancia, enseñar a los niños a ser empáticos y a respetar las emociones y necesidades de los demás les ayuda a construir relaciones más equilibradas y significativas, o como lo dicen Shonkoff et al. (2012), \"Las relaciones afectivas tempranas actúan como un amortiguador que protege al niño del estrés tóxico, permitiendo un desarrollo emocional y cognitivo saludable\"; es decir, que las experiencias de cercanía o falta de ella durante la infancia, tienen un impacto directo en el desarrollo cerebral y emocional. Las relaciones equilibradas atenúan el estrés y promueven el bienestar.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Influencia del entorno"}},[_c('p',[_vm._v("El ambiente en el que los niños crecen, juega un papel crucial en cómo manejan esta dinámica de acercamiento y distancia (Ainsworth, 1978; Vygotsky, 1978). Un entorno cálido, afectuoso y respetuoso, donde los cuidadores les brinden seguridad emocional pero también les permitan explorar su independencia, fomenta el desarrollo de habilidades sociales saludables; en este sentido, Rogers (1961), argumentó que la aceptación incondicional y la empatía, son esenciales para el desarrollo personal. Los niños, como los erizos, necesitan sentirse aceptados y seguros en sus relaciones para poder crecer emocionalmente, pero sin miedo a ser rechazados o heridos.")])])])],1)]),_vm._m(5),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-up"}},[_vm._v("El juego es un elemento esencial en el desarrollo social y emocional de los niños, ya que les permite explorar roles, expresar emociones y aprender habilidades interpersonales de forma natural.")]),_c('div',{staticClass:"row bg-azul bg-full-width py-4 mb-4"},[_c('div',{staticClass:"px-5 pb-md-3 py-4 d-flex"},[_vm._m(6),_c('div',{staticClass:"col-12 col-xl-7 d-flex ps-4 ps-xl-5 align-items-center flex-column",attrs:{"data-aos":"fade-left"}},[_c('h2',{staticClass:"mb-4 pb-2 me-auto"},[_vm._v("El juego")]),_c('p',{staticClass:"mb-5"},[_vm._v("En el PDF El juego, se analiza cómo las actividades lúdicas favorecen la socialización, fomentan la empatía y fortalecen la comunicación entre pares, además de abordar los desafíos y beneficios que el juego aporta al crecimiento integral durante esta etapa.")]),_c('a',{staticClass:"d-flex me-auto w-fit bg-white box-shadow cursor-pointer",attrs:{"data-aos":"zoom-in","href":_vm.obtenerLink('/downloads/Anexos_El juego como el motor principal de las relaciones sociales en la infancia.pdf'),"target":"_blank"}},[_c('img',{staticClass:"h-100",staticStyle:{"width":"48px"},attrs:{"src":require("@/assets/componentes/pdf-icon-square.svg"),"alt":"Texto que describa la imagen"}}),_vm._m(7)])])])]),_vm._m(8),_c('p',{st