ra vez. Este ciclo de acercarse y alejarse continuó hasta que, poco a poco, los erizos encontraron una distancia justa: lo suficientemente cerca como para compartir calor, pero no tanto como para lastimarse mutuamente con sus púas.")])]),_c('div',{staticClass:"col-6 d-none d-xl-block"},[_c('img',{staticClass:"h-100",attrs:{"src":require("@/assets/curso/unidad/img-41.png"),"alt":"Texto que describa la imagen"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"d-none d-xl-block col-5",attrs:{"data-aos":"fade-right"}},[_c('img',{staticClass:"h-100",attrs:{"src":require("@/assets/curso/unidad/img-42.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-7"},[_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-up"}},[_c('b',[_vm._v("Moraleja:")]),_vm._v(" aunque todos necesitamos cercanía y afecto, también es importante respetar los límites individuales para evitar causar daño a los demás o a nosotros mismos. La convivencia saludable requiere equilibrio entre la intimidad y el espacio personal.")]),_c('span',[_vm._v("Quisiera aprovechar esta fábula para que analicemos algunos aspectos relevantes de la importancia de las relaciones interpersonales en la infancia:")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4 mx-0"},[_c('div',{staticClass:"col-12 col-xl-8 d-flex align-items-center bg-verde p-4",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("Como lo hemos venido viendo a lo largo de la unidad, la infancia es una etapa crítica en la que los niños desarrollan habilidades sociales y emocionales que les permiten interactuar con otros. Estas relaciones se dan en distintos contextos, como el familiar, escolar y social y, según (Papalia et al., 2017), son esenciales para la construcción de habilidades como la empatía, la co